import React, { PureComponent } from 'react';
import { DatePicker } from 'material-ui-pickers';

class DateSelector extends PureComponent {
  state = {
    selectedDate: new Date(),
  };

  handleDateChange = date => {
    this.setState({
      selectedDate: date,
    });
  };

  render() {
    const { selectedDate } = this.state;
    const {
      id,
      label,
      endAdornment,
      hasAdornment,
      helperText,
    } = this.props;
    return hasAdornment ? (
      <DatePicker
        id={id}
        label={label}
        format="yyyy/MM/dd"
        name="arrivalDate"
        fullWidth
        value={selectedDate}
        onChange={this.handleDateChange}
        adornmentPosition="start"
        variant="outlined"
        margin="dense"
        InputProps={endAdornment}
        keyboard
        mask={value =>
          value
            ? [/\d/, /\d/, /\d/, /\d/, '/', /\d/, /\d/, '/', /\d/, /\d/]
            : []
        }
      />
    ) : (
      <DatePicker
        id={id}
        label={label}
        format="yyyy/MM/dd"
        placeholder="2018/12/31"
        name="arrivalDate"
        helperText={helperText}
        fullWidth
        value={selectedDate}
        onChange={this.handleDateChange}
        variant="outlined"
        margin="dense"
      />
    );
  }
}

export default DateSelector;
