import React, { PureComponent, Fragment } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import {
  List,
  Avatar,
  ListItem,
  ListItemIcon,
  ListItemText,
  SwipeableDrawer,
  Divider,
} from '@material-ui/core';
import { Link } from 'react-router-dom';

const styles = theme => ({
  list: {
    width: 250,
  },
  primaryAvatar: {
    backgroundColor: theme.palette.secondary.main,
  },
  secondaryAvatar: {
    backgroundColor: theme.palette.primary.main,
  },
});

class MainDrawer extends PureComponent {
  render() {
    const { classes, menuList, links } = this.props;

    return (
      <div>
        <SwipeableDrawer
          open={this.props.open}
          onClose={this.props.onClose}
          onOpen={this.props.onOpen}
        >
          <div className={classes.list}>
            <List>
              {menuList.map((item, index) => (
                <Fragment key={item.primary}>
                  {index === 2 && <Divider />}
                  <ListItem button component={Link} to={links[index]}>
                    <ListItemIcon>
                      {index > 1 ? (
                        <Avatar className={classes.primaryAvatar}>
                          {index + 1}
                        </Avatar>
                      ) : (
                        <Avatar className={classes.secondaryAvatar}>
                          {index + 1}
                        </Avatar>
                      )}
                    </ListItemIcon>
                    <ListItemText
                      primary={item.primary}
                      secondary={item.secondary}
                    />
                  </ListItem>
                </Fragment>
              ))}
            </List>
          </div>
        </SwipeableDrawer>
      </div>
    );
  }
}

MainDrawer.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MainDrawer);
