import React, { PureComponent, Fragment } from 'react';
import { compose } from 'recompose';
import {
  withStyles,
  withWidth,
  Paper,
  Stepper,
  Step,
  StepLabel,
  Button,
  Typography,
} from '@material-ui/core';

const styles = theme => ({
  layout: {
    width: 'auto',
    [theme.breakpoints.up(650 + theme.spacing.unit * 2 * 2)]: {
      width: 650,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginBottom: theme.spacing.unit * 3,
    padding: theme.spacing.unit * 3,
    [theme.breakpoints.up(650 + theme.spacing.unit * 3 * 2)]: {
      marginTop: theme.spacing.unit * 6,
      marginBottom: theme.spacing.unit * 6,
      padding: theme.spacing.unit * 3,
    },
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing.unit * 3,
    marginLeft: theme.spacing.unit,
  },
  stepper: {
    padding: `${theme.spacing.unit * 3}px 0 ${theme.spacing.unit * 5}px`,
  },
});

class FormPage extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
      key: 0,
    };
  }

  componentDidUpdate() {
    console.log('updated');
  }

  handleNext = () => {
    this.setState(state => ({
      activeStep: state.activeStep + 1,
    }));
  };

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
    }));
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };

  getStepContent = step => {
    const { componentList } = this.props;
    return React.cloneElement(componentList[step], {
      handleNext: this.handleNext,
    });
  };

  render() {
    const { classes, labels, mainHeading, subHeading } = this.props;
    const { activeStep } = this.state;
    const steps =
      this.props.width === 'xs' ? labels.mobile : labels.desktop;
    return (
      <Fragment>
        <main className={classes.layout}>
          <Paper className={classes.paper}>
            <Typography
              color="textSecondary"
              variant="h5"
              align="center"
              gutterBottom
            >
              {mainHeading}
            </Typography>
            <Typography align="center" variant="h5" gutterBottom>
              {subHeading}
            </Typography>
            <Stepper activeStep={activeStep} className={classes.stepper}>
              {steps.map(label => (
                <Step key={label}>
                  <StepLabel>{label}</StepLabel>
                </Step>
              ))}
            </Stepper>
            <Fragment>
              {activeStep === steps.length ? (
                Object.entries(localStorage).map(key => (
                  <Fragment key={key}>
                    <Typography
                      color="textSecondary"
                      variant="h6"
                      gutterBottom
                    >
                      {key[0]}
                    </Typography>
                    <Typography color="textPrimary" variant="subtitle1">
                      {key[1]}
                    </Typography>
                  </Fragment>
                ))
              ) : (
                <Fragment>
                  {this.getStepContent(activeStep)}
                  <div className={classes.buttons}>
                    {activeStep !== 0 && (
                      <Button
                        onClick={this.handleBack}
                        className={classes.button}
                      >
                        Back
                      </Button>
                    )}
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      onClick={this.handleNext}
                      className={classes.button}
                    >
                      {activeStep === steps.length - 1 ? 'Submit' : 'Next'}
                    </Button>
                  </div>
                </Fragment>
              )}
            </Fragment>
          </Paper>
        </main>
      </Fragment>
    );
  }
}

export default compose(
  withStyles(styles),
  withWidth()
)(FormPage);
