import React, { PureComponent } from 'react';
import ShippingMarks from '../fields/IDC1Fields/idc3Fields/ShippingMarks';
import { ValidatorForm } from 'react-material-ui-form-validator';
import FieldHeader from '../../shared/FieldHeader';
import TotalPackageAmount from '../fields/IDC1Fields/idc3Fields/TotalPackageAmount';
import TotalPackageUnitCode from '../fields/IDC1Fields/idc3Fields/TotalPackageUnitCode';
import CurrencyCode from '../fields/IDC1Fields/idc3Fields/CurrencyCode';
import ExchangeRate from '../fields/IDC1Fields/idc3Fields/ExchangeRate';
import CIFValueForeign from '../fields/IDC1Fields/idc3Fields/CIFValueForeign';
import CIFValueBaht from '../fields/IDC1Fields/idc3Fields/CIFValueBaht';
import WeightControl from '../fields/IDC1Fields/idc3Fields/WeightControl';
import SubmitButton from '../../shared/SubmitButton';

class IDC1FormWrapper3 extends PureComponent {
  constructor() {
    super();
    this.cifValueForeignRef = React.createRef();
    this.exchangeRateRef = React.createRef();
  }

  saveToLocalStorage = data => {
    for (const pair of data.entries()) {
      localStorage.setItem(pair[0], pair[1]);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
  };

  render() {
    return (
      <ValidatorForm
        onSubmit={this.handleSubmit}
        noValidate
        autoComplete="off"
      >
        <FieldHeader>รายละเอียดการขนส่ง (ต่อ) </FieldHeader>
        <ShippingMarks />
        <TotalPackageAmount />
        <TotalPackageUnitCode />
        <WeightControl />
        <FieldHeader>ข้อมูลด้านการเงิน</FieldHeader>
        <CurrencyCode />
        <ExchangeRate ref={this.exchangeRateRef} />
        <CIFValueForeign ref={this.cifValueForeignRef} />
        <CIFValueBaht
          cifValueForeignRef={this.cifValueForeignRef}
          exchangeRateRef={this.exchangeRateRef}
        />
        <SubmitButton />
      </ValidatorForm>
    );
  }
}

export default IDC1FormWrapper3;
