import React, { PureComponent } from 'react';
import { SelectValidator } from 'react-material-ui-form-validator';
import { MenuItem, withWidth } from '@material-ui/core';
import { handleChange } from './handleChange';

class RegularSelect extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
      currentWidth: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  componentDidMount() {
    setTimeout(() => {
      this.forceUpdate();
    }, 500);
  }

  render() {
    const { name, label, helperText, options, width } = this.props;
    return (
      <SelectValidator
        id={name}
        required
        SelectProps={{
          native: width === 'xs' ? true : false,
        }}
        InputLabelProps={
          width === 'xs'
            ? {
                shrink: true,
              }
            : null
        }
        name={name}
        onChange={this.handleChange}
        value={this.state.value}
        select
        label={label}
        helperText={helperText}
        fullWidth
        validators={['required']}
        errorMessages={['จำเป็นต้องเลือก']}
        variant="outlined"
        margin="dense"
      >
        {options.map(option =>
          width !== 'xs' ? (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ) : (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          )
        )}
      </SelectValidator>
    );
  }
}

export default withWidth()(RegularSelect);
