import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../shared/handleChange';
import { InputAdornment } from '@material-ui/core';
import AdditionalInfoDialog from './AdditionalInfoDialog';

class RegularTextField extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  componentDidMount() {
    setTimeout(() => {
      this.forceUpdate();
    }, 500);
  }

  render() {
    const {
      name,
      label,
      helperText,
      placeholder,
      validators,
      errorMessages,
      required,
      type,
      dialogTitleTH,
      dialogFormat,
      dialogContent,
      dialogWidth,
    } = this.props;
    return (
      <TextValidator
        name={name}
        id={name}
        required={required}
        onBlur={this.handleBlur}
        type={type}
        onChange={this.handleChange}
        value={this.state.value}
        label={label}
        helperText={helperText}
        margin="dense"
        placeholder={placeholder}
        variant="outlined"
        fullWidth
        validators={validators}
        errorMessages={errorMessages}
        InputProps={{
          endAdornment: dialogContent && (
            <InputAdornment position="end">
              <AdditionalInfoDialog
                maxWidth={dialogWidth ? dialogWidth : 'xs'}
                titleTH={dialogTitleTH ? dialogTitleTH : helperText}
                titleEN={label}
                format={dialogFormat}
              >
                {dialogContent}
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default RegularTextField;
