import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../../shared/handleChange';

class TotalPackageAmount extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  handleBlur = e => {
    parseInt(e.target.value) === 0
      ? this.setState({
          value: '0',
        })
      : this.setState({
          value: e.target.value,
        });
  };

  render() {
    return (
      <TextValidator
        required
        name="totalPackageAmount"
        id="totalPackageAmount"
        onChange={this.handleChange}
        onBlur={this.handleBlur}
        value={this.state.value}
        label="Total Package Amount"
        helperText="จำนวนหีบห่อรวม"
        placeholder="00000000"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[0-9]*$',
          'maxStringLength:8',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกได้เฉพาะตัวเลข',
          'กรอกได้สูงสุด 8 หลัก',
        ]}
        margin="dense"
        variant="outlined"
      />
    );
  }
}

export default TotalPackageAmount;
