import React, { Fragment, Component } from 'react';
import { hot } from 'react-hot-loader';
import './App.css';
import {
  MuiThemeProvider,
  createMuiTheme,
  CssBaseline,
} from '@material-ui/core';
import DateFnsUtils from '@date-io/date-fns';
import thLocale from 'date-fns/locale/th';
import { MuiPickersUtilsProvider } from 'material-ui-pickers';
import { BrowserRouter, Route } from 'react-router-dom';
import TopNav from './components/TopNav';

import IDC1MainFormPage from './components/mainForms/IDC1MainFormPage';
import IDC2MainFormPage from './components/mainForms/IDC2MainFormPage';

const THEME = createMuiTheme({
  typography: {
    fontFamily: 'Noto Sans Thai, Noto',
    fontSize: 15,
    useNextVariants: true,
  },
  palette: {
    secondary: {
      light: '#f05545',
      main: '#b71c1c',
      dark: '#7f0000',
      contrastText: '#fff',
    },
    primary: {
      light: '#48a999',
      main: '#00796b',
      dark: '#004c40',
      contrastText: '#fff',
    },
  },
});

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <Fragment>
          <CssBaseline />
          <MuiThemeProvider theme={THEME}>
            <MuiPickersUtilsProvider
              utils={DateFnsUtils}
              locale={thLocale}
            >
              <Fragment>
                <TopNav />
                <Route path="/" component={IDC1MainFormPage} exact />
                <Route path="/idc1" component={IDC1MainFormPage} />
                <Route path="/idc2" component={IDC2MainFormPage} />
              </Fragment>
            </MuiPickersUtilsProvider>
          </MuiThemeProvider>
        </Fragment>
      </BrowserRouter>
    );
  }
}

export default hot(module)(App);
